/**
 * Say formatlama ve ileme yardmc fonksiyonlar
 * Bu dosya, uygulamada kullanlan tm saysal ilemleri ierir
 * Trke say formatn (1.234.567,89) destekler ve gvenli dnmler salar
 */

/**
 * Bir sayy Trke formatta (1.234.567,89) formatlar
 * Bu fonksiyon, kullancnn girdii saylar grsel olarak daha okunabilir hale getirir
 * Binlik ayrc olarak nokta, ondalk ayrc olarak virgl kullanr
 *
 * @param {string|number} value - Formatlanacak say (string veya number)
 * @returns {string} Trke formatnda say string'i veya bo string
 *
 * rnek kullanmlar:
 * formatNumber('1234567.89')  '1.234.567,89'
 * formatNumber('1000')  '1.000'
 * formatNumber('abc')  ''
 */
export const formatNumber = (value) => {
  if (!value) return '';

  try {
    // Girdi deerini string'e evir ve gereksiz karakterleri temizle
    // Sadece rakam, nokta ve virgle izin ver
    const cleanedValue = String(value)
      .replace(/[^\d.,]/g, '') // Sadece rakam, nokta ve virgl
      .replace(/\./g, '') // Tm noktalar kaldr (bunlar binlik ayrc olabilir)
      .replace(',', '.'); // Virgl noktaya evir (ondalk ayrc iin)

    const numericValue = parseFloat(cleanedValue);

    // NaN kontrol - eer sayya evrilemezse bo string dndr
    if (isNaN(numericValue)) return '';

    // Intl.NumberFormat ile Trke locale kullanarak formatla
    // Bu sayede binlik ayrc ve ondalk ayrc otomatik olarak doru yerleir
    return new Intl.NumberFormat('tr-TR', {
      minimumFractionDigits: 0,
      maximumFractionDigits: 2,
    }).format(numericValue);
  } catch (_error) { // eslint-disable-line no-unused-vars
    // Say formatlama hatas
    return '';
  }
};

/**
 * Trke formatndaki sayy normal float deerine evirir
 * Bu fonksiyon, formatNumber'n tersi ilemi yapar
 * Kullancdan alnan formatlanm sayy hesaplamalarda kullanlabilir hale getirir
 *
 * @param {string} formattedValue - Trke formatnda say (rn: '1.234.567,89')
 * @returns {string} Normal float formatnda say string'i veya bo string
 *
 * rnek kullanmlar:
 * parseFormattedNumber('1.234.567,89')  '1234567.89'
 * parseFormattedNumber('1.000')  '1000'
 * parseFormattedNumber('abc')  ''
 */
export const parseFormattedNumber = (formattedValue) => {
  if (!formattedValue) return '';

  try {
    // Trke format (1.234,56)  float'a evir (1234.56)
    const cleanedValue = formattedValue
      .replace(/\./g, '') // Binlik ayrc noktalar kaldr
      .replace(',', '.'); // Ondalk virgl noktaya evir

    const numericValue = parseFloat(cleanedValue);
    return isNaN(numericValue) ? '' : numericValue.toString();
  } catch (_error) { // eslint-disable-line no-unused-vars
    // Say parse hatas
    return '';
  }
};

/**
 * Kullanc girdisini temizler - sadece rakamlar brakr
 * Input field'larda kullancnn sadece rakam girebilmesi iin kullanlr
 *
 * @param {string} value - Temizlenecek string
 * @returns {string} Sadece rakamlar ieren string
 */
export const cleanNumericValue = (value) => {
  if (!value) return '';
  return value.toString().replace(/[^\d]/g, '');
};
