/**
 * Form Validasyon Yardmc Fonksiyonlar
 * Kullanc deneyimini bozmadan arka planda validasyon kontrol yapar
 */

import { BrandTypes, CategoryTypes } from '../constants/types';

/**
 * Seri numaras validasyonu
 * @param {string} serial - Seri numaras
 * @param {string} selectedBrand - Seili marka
 * @param {string} selectedCategory - Seili kategori
 * @returns {boolean} - Geerli mi?
 */
export const validateSerial = (serial, selectedBrand, selectedCategory) => {
  // Cufflinks (AKSESUAR) iin seri numaras zorunlu deil
  if (
    selectedBrand === BrandTypes.ROLEX &&
    selectedCategory === CategoryTypes.AKSESUAR
  ) {
    return true;
  }

  if (!serial || !serial.trim()) {
    return false;
  }

  const trimmedSerial = serial.trim();
  const hasLetter = /[a-zA-Z]/.test(trimmedSerial);
  const hasNumber = /[0-9]/.test(trimmedSerial);

  // En az 1 harf ve 1 rakam kontrol
  if (!hasLetter || !hasNumber) {
    return false;
  }

  // Marka baznda karakter says kontrol
  if (selectedBrand === BrandTypes.ROLEX) {
    // Rolex: 8 karakter
    return trimmedSerial.length >= 8;
  }

  if (selectedBrand === BrandTypes.TUDOR) {
    // Tudor: 7 karakter
    return trimmedSerial.length >= 7;
  }

  return false;
};

/**
 * Genel form validasyonu
 * @param {Object} formData - Form verileri
 * @param {string} selectedBrand - Seili marka
 * @param {string} selectedCategory - Seili kategori
 * @param {boolean} isProductChecked - rn kontrol onay
 * @param {Object} rmcAnalysisResult - RMC analiz sonucu
 * @param {Object} rmcMessage - RMC mesaj
 * @returns {boolean} - Form geerli mi?
 */
export const validateForm = (
  formData,
  selectedBrand,
  selectedCategory,
  isProductChecked,
  rmcAnalysisResult,
  rmcMessage
) => {
  // Zorunlu alanlar kontrol
  const requiredFields = ['musteri', 'rmc', 'teslimEdilenKisi'];
  const requiredFieldsWithSerial = [...requiredFields];

  // Cufflinks (AKSESUAR) deilse seri numaras da zorunlu
  if (
    !(
      selectedBrand === BrandTypes.ROLEX &&
      selectedCategory === CategoryTypes.AKSESUAR
    )
  ) {
    requiredFieldsWithSerial.push('seri');
  }

  const hasAllRequiredFields = requiredFieldsWithSerial.every((field) => {
    return formData[field] && formData[field].trim().length > 0;
  });

  if (!hasAllRequiredFields) {
    return false;
  }

  // RMC geerlilik kontrol
  const isRmcValid =
    formData.rmc && rmcAnalysisResult && rmcMessage.type !== 'error';

  if (!isRmcValid) {
    return false;
  }

  // Seri numaras validasyonu
  const isSerialValid = validateSerial(
    formData.seri,
    selectedBrand,
    selectedCategory
  );
  if (!isSerialValid) {
    return false;
  }

  // rn kontrol onay
  if (!isProductChecked) {
    return false;
  }

  return true;
};

/**
 * Seri numaras iin minimum karakter saysn dndrr
 * @param {string} selectedBrand - Seili marka
 * @param {string} selectedCategory - Seili kategori
 * @returns {number} - Minimum karakter says
 */
export const getSerialMinLength = (selectedBrand, selectedCategory) => {
  // Cufflinks (AKSESUAR) iin seri numaras gerekli deil
  if (
    selectedBrand === BrandTypes.ROLEX &&
    selectedCategory === CategoryTypes.AKSESUAR
  ) {
    return 0;
  }

  if (selectedBrand === BrandTypes.ROLEX) {
    return 8;
  }

  if (selectedBrand === BrandTypes.TUDOR) {
    return 7;
  }

  return 0;
};

/**
 * Seri numaras iin aklama metni dndrr
 * @param {string} selectedBrand - Seili marka
 * @param {string} selectedCategory - Seili kategori
 * @returns {string} - Aklama metni
 */
export const getSerialDescription = (selectedBrand, selectedCategory) => {
  // Cufflinks (AKSESUAR) iin seri numaras aklamas gerekli deil
  if (
    selectedBrand === BrandTypes.ROLEX &&
    selectedCategory === CategoryTypes.AKSESUAR
  ) {
    return 'Cufflinks iin seri numaras gerekmez';
  }

  const minLength = getSerialMinLength(selectedBrand, selectedCategory);

  if (minLength === 0) {
    return '';
  }

  return `En az ${minLength} karakter, en az 1 harf ve 1 rakam iermeli`;
};
