/**
 * Mteri bilgileri form bileeni
 * Mteri ad ve teslim edilen kii input'larn ynetir
 */

import { memo } from 'react';

const CustomerForm = memo(({
  formData,
  validationErrors,
  onChange
}) => {
  // Not: Input background renkleri artk CSS variable'lar ile ynetiliyor (useTheme hook'u)

  return (
    <>
      {/* Mteri */}
      <div className="w-full flex flex-col gap-1 mb-2">
        <label htmlFor="musteri" className="font-semibold text-xs text-[#003057]">Mteri Ad Soyad</label>
        <input
          required
          className={`input-filled outline-none shadow-none rounded-lg px-3 py-2 text-sm w-full border ${
            validationErrors.musteri ? 'border-red-500' : 'border-border-gray'
          }`}
          id="musteri"
          type="text"
          name="musteri"
          placeholder="Mteri Ad Soyad"
          value={formData.musteri}
          data-has-value={formData.musteri ? "true" : "false"}
          onChange={onChange}
        />
        {validationErrors.musteri && (
          <span className="text-xs text-red-500 font-medium" role="alert">
            {validationErrors.musteri}
          </span>
        )}
      </div>

      {/* Teslim Edilen Kii */}
      <div className="w-full flex flex-col gap-1 mb-2">
        <label htmlFor="teslimEdilenKisi" className="font-semibold text-xs text-[#003057]">Teslim Edilen Kii</label>
        <input
          required
          className={`input-filled outline-none shadow-none rounded-lg px-3 py-2 text-sm w-full border ${
            validationErrors.teslimEdilenKisi ? 'border-red-500' : 'border-border-gray'
          }`}
          id="teslimEdilenKisi"
          type="text"
          name="teslimEdilenKisi"
          placeholder="Teslim Edilen Kii"
          value={formData.teslimEdilenKisi}
          data-has-value={formData.teslimEdilenKisi ? "true" : "false"}
          onChange={onChange}
        />
        {validationErrors.teslimEdilenKisi && (
          <span className="text-xs text-red-500 font-medium" role="alert">
            {validationErrors.teslimEdilenKisi}
          </span>
        )}
      </div>
    </>
  );
});

CustomerForm.displayName = 'CustomerForm';

export default CustomerForm;
