import { format } from 'date-fns';

/**
 * Gncel tarihi Trke formatnda (dd.MM.yyyy) dndrr
 * @returns {string} Bugnn tarihi dd.MM.yyyy formatnda
 */
export const getCurrentDate = () => {
  return format(new Date(), 'dd.MM.yyyy');
};
