import { useState, useCallback } from 'react';

import { BrandTypes } from '../constants/types';
import { toTurkishUpperCase } from '../utils/stringHelpers';

export const useFormData = (initialFormData, selectedBrand) => {
  const [formData, setFormData] = useState(initialFormData);

  /**
   * Seri numaras ileme fonksiyonu
   * Marka bazl karakter snrlamas ve formatlamas
   */
  const handleSeriChange = useCallback(
    (value) => {
      let cleanValue;

      if (selectedBrand === BrandTypes.TUDOR) {
        cleanValue = toTurkishUpperCase(
          value.replace(/[^a-zA-Z0-9]/g, '').substring(0, 7)
        );
      } else {
        cleanValue = toTurkishUpperCase(
          value.replace(/[^a-zA-Z0-9]/g, '').substring(0, 8)
        );
      }

      setFormData((prev) => ({ ...prev, seri: cleanValue }));
    },
    [selectedBrand]
  );

  /**
   * Size field ileme fonksiyonu
   * Sadece rakamlar kabul eder
   */
  const handleSizeChange = useCallback((value) => {
    const numericValue = value.replace(/[^\d]/g, '');
    setFormData((prev) => ({ ...prev, size: numericValue }));
  }, []);

  /**
   * Genel field ileme fonksiyonu
   * Dier string fieldlar iin kullanlr
   */
  const handleGeneralFieldChange = useCallback((name, value) => {
    setFormData((prev) => ({ ...prev, [name]: toTurkishUpperCase(value) }));
  }, []);

  /**
   * Ana form change handler
   * Field tipine gre doru ileme fonksiyonunu arr
   */
  const handleChange = useCallback(
    (e) => {
      const { name, value } = e.target;

      switch (name) {
        case 'rmc':
          // RMC deiikliini debounce ile yap - burada sadece state gncellenir, analiz blur eventnda tetiklenir
          setFormData((prev) => ({ ...prev, rmc: toTurkishUpperCase(value) }));
          break;
        case 'seri':
          handleSeriChange(value);
          break;
        case 'size':
          handleSizeChange(value);
          break;
        case 'description':
        case 'kadran':
        case 'bilezik':
          handleGeneralFieldChange(name, value); // Bu alanlar iin genel handler
          break;
        case 'fiyat':
          // Fiyat alan hook iinde deil, darda RMC analizinden gncelleniyor.
          // Burada sadece olas manuel giri iin bir placeholder brakyoruz,
          // ancak u anki mantkta RMC analizi fiyat set ediyor.
          // htiya olursa buras gelitirilebilir.
          break;
        default:
          handleGeneralFieldChange(name, value);
          break;
      }
    },
    [handleSeriChange, handleSizeChange, handleGeneralFieldChange]
  );

  // RMC blur handler' hook iinde tutulmayacak, ana bileende useRmcAnalysis hooku ile birlikte kullanlacak.

  return {
    formData,
    setFormData, // Ana bileenin form datasn gncellemek iin da aktaryoruz
    handleChange,
  };
};
